import { FC, useCallback, useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";

import {
  Button,
  Input,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";

import {
  resetPasswordErrorSelector,
  resetPasswordLoadingSelector,
  resetPasswordResettedSelector,
} from "../../services/selectors/resetPasswordSelector";
import { resetPassword } from "../../services/actions/resetPasswordActions";
import { forgotPasswordAllowedSelector } from "../../services/selectors/forgotPasswordSelectors";

import "../../styles/form.css";
import { useAppDispatch, useAppSelector } from "../../types";

const ResetPasswordPage: FC = () => {
  const [password, setPassword] = useState("");
  const [token, setToken] = useState("");
  const isLoading = useAppSelector(resetPasswordLoadingSelector);
  const isResetted = useAppSelector(resetPasswordResettedSelector);
  const error = useAppSelector(resetPasswordErrorSelector);
  const isAllowed = useAppSelector(forgotPasswordAllowedSelector);

  const history = useHistory();
  const dispatch = useAppDispatch();

  const handlePasswordChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setPassword(e.target.value);
    },
    []
  );

  const handleCodeChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setToken(e.target.value);
    },
    []
  );

  const handlePasswordReset = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(resetPassword({ password, token }));
  };

  useEffect(() => {
    if (isResetted) {
      history.push("/login");
    }
  }, [isResetted, history]);

  useEffect(() => {
    if (!isAllowed) {
      history.push("/forgot-password");
    }
  }, [history, isAllowed]);

  return (
    <div className="form">
      <h2 className="form__title text text_type_main-medium">
         
      </h2>
      {error && (
        <p className="form__text text text_type_main-default text_color_error pb-4">
              
        </p>
      )}
      <form className="form__content" onSubmit={handlePasswordReset}>
        <PasswordInput
          name="password"
          value={password}
          onChange={handlePasswordChange}
          placeholder="  "
          disabled={isLoading}
        />
        <Input
          name="token"
          value={token}
          onChange={handleCodeChange}
          placeholder="   "
          disabled={isLoading}
        />
        <Button
          htmlType="submit"
          size="large"
          type="primary"
          disabled={isLoading}
        >
          {isLoading ? "" : ""}
        </Button>
      </form>
      <p className="form__text text text_type_main-default text_color_inactive">
         ?{" "}
        <Link className="text_color_accent" to="/login">
          
        </Link>
      </p>
    </div>
  );
};

export default ResetPasswordPage;
